/************************************************

Stylesheet: Main Stylesheet

*************************************************/

/*********************
GENERAL STYLES
*********************/

body {
	font-family: inherit;
}

#header,
#main,
#footer {
}

.header-inner,
.main-inner,
.footer-inner {
}

figure {
	margin: 0;
}

.section__slice p,
.story__editorialintro p {
	font-size: $body-font-size;
}

/*********************
LINK STYLES
*********************/
a, a:visited {
	color: $black;
	// Mobile tap color
	&:link {
		//Highlight links on iOS, Similar to :hover for mobile devices.
		-webkit-tap-highlight-color : rgba( 0, 0, 0, 0.3 );
	}
}

.menu li.active > a {
	background: none;
	color: $black;
	//border-bottom: 2px solid $yellow-tandem;
}

/*********************
H1, H2, H3, H4, H5 P STYLES
*********************/
h1, .h1, h2, .h2, h3, .h3, h4, .h4, h5, .h5 {

	// Removing text decoration from all headline links
	a {
		text-decoration: none;
	}
}

h1, .h1 {

}

h2, .h2 {

}

h3, .h3 {

}

h4, .h4 {

}

h5, .h5 {

}

/*********************
BANNER STYLES
*********************/

#header {
	position: relative;
	width: 100%;
}

.inner-header {
	position: fixed;
	width: 100%;
	z-index: 20;
}

#main {
	padding-top: 4.5em;
}

/*********************
NAVIGATION STYLES
*********************/

nav.top-bar {
	background-color: $white;
}

nav.top-bar {
	border-bottom: 1px solid $light-gray;
}

ul#menu-main-menu-left,
ul#menu-main-menu-right {
	display: inline-block;
	box-sizing: border-box;
	background-color: $white;
	li {
		text-align: center;
		a {
			line-height: 1.5em;
		}
	}
}

ul#menu-main-menu-left {
	padding-right: 6em;
	width: 50%;
	li {
		float: right;
	}
}

ul#menu-main-menu-right {
	padding-left: 6em;
	width: 40%;
	li {
		float: left;
	}
}

.top-bar__center {
	position: relative;
	a {
		position: absolute;
		display: block;
		left: 50%;
		right: 50%;
		width: 10em;
		margin-left: -5em;
	}
}

.top-bar__search {
	display: none;
}

/*********************
ARTICLE STYLES
*********************/

.story__header__introduction,
.story__header__lowres-image-wrapper,
.story__content,
.story__footer,
.story__relatedgrid {
	@include grid-row;
}

.story__extras {
	padding-bottom: 2 * $global-padding;
	padding-top: 2 * $global-padding;
	margin-bottom: 2 * $global-margin;
	margin-top: 2 * $global-margin;
}

.story__header__image.lowres,
.story__editorialintro,
.story__title-wrapper,
.story__intro,
.section__slice,
.story__footer__byline {
	margin-bottom: 2 * $global-margin;
	margin-top: 2 * $global-margin;
	@include grid-column();
	float: none !important;
	@include breakpoint( small ) {
		@include grid-column( 95% );
		@include grid-column-position( center );
	}
	@include breakpoint( medium ) {
		@include grid-column( 85% );
		@include grid-column-position( center );
	}
	@include breakpoint( large ) {
		@include grid-column( 66.666667% );
		@include grid-column-position( center );
	}
	@include breakpoint( xlarge ) {
		@include grid-column( 60% );
		@include grid-column-position( center );
	}
}

.story__relatedgrid {
	@include grid-column( 95%, 0 );
	@include grid-column-position( center );
}

/*********************
ARTICLE HEADER STYLES
*********************/
.article-header {
	ul.off-canvas-list li {
		list-style: none;
	}
}


/*********************
ARTICLE PATTERN STYLES
*********************/


.section__slice:first-of-type {
	margin-top: 0;
}

.story__section p,
.story__section li {
	font-weight: $global-weight-light;
}

/***************
Pattern 1. Title
****************/

body.page .story__title-wrapper {
	text-align: center;
}


/*****************
Pattern 2. Bylines
******************/

/********************************
Pattern 3. Editorial introduction
*********************************/

.story__editorialintro {
	font-size: $lead-font-size;
	line-height: $lead-lineheight;
	font-style: italic;
}

body.page .story__editorialintro {
	font-style: normal;
	text-align: center;
	margin-bottom: 4 * $global-margin;
}

/******************
Pattern 4. Tag list
******************/

.story__meta__tags ol,
.griditem__tags ol {
	list-style: none;
	margin-left: 0;
}

.griditem__tags ol li {
	display: inline;
}

.story__meta__tags ol li {
	display: block;
	margin: 0.125 * $global-margin 0;
}


.tag, .griditem__tag, .story__meta__tag {
	@include label;
	font-size: $small-font-size;
	&.post_tag {
		border: 1px solid $yellow-tandem;
	}
	&.topic {
		border: 1px solid $yellow-2;
	}
	&.language {
		border: 1px solid $salmon-4;
	}
	&.location {
		border: 1px solid $blue-3;
	}
}

/****************************
Pattern 5. Hero / cover image
*****************************/

.story__header__image-wrapper {
	height: 12em;
	@include breakpoint( medium ) {
		height: 20em;
	}
	@include breakpoint( large ) {
		height: 25em;
	}
	@include breakpoint( xlarge ) {
		height: 30em;
	}
	@include breakpoint( xxlarge ) {
		height: 35em;
	}
	overflow: hidden;
	position: relative;
	img {
		@include vertical-center;
	}

}

/*********************
Pattern 6. Paragraph
*********************/

/*******************
Pattern 7. Subheader
********************/

.section__subheader {
	margin-bottom: 0;
}

.section__subheader + .section__paragraph {
	margin-top: 0;
}

/************************
Pattern 8. Embedded Video
*************************/

/****************
Pattern 9. Image
****************/

.section__imageduo,
.section__simplemap.simplemap--wide {
	@include grid-column( 95%,0 );
	padding-top: 2 * $global-padding;
	padding-bottom: $global-padding;
	background-color: $white;
	z-index: 10;
	position: relative;
	z-index: 10;
}

.imageduo__wrapper {
	@include grid-row;
	@include breakpoint( medium ) {
		@include grid-row-nest;
	}
}

.imageduo__image {
	@include grid-column;
	@include grid-column-collapse;
	@include breakpoint( small ) {
		margin-bottom: 2*$global-margin;
	}
	@include breakpoint( medium ) {
		@include grid-column( 6 );
	}
}

figcaption {
	margin-top: $global-margin;
	font-size: $small-font-size;
	color: $dark-gray;
}

