/**************************
Pattern20. Emphasis blocks
***************************/

.section__emphasisblock {
	font-weight: $global-weight-semibold;
	padding-top: $global-padding;
	padding-bottom: $global-padding;
	@include breakpoint( small) {
		@include grid-column( 100% );
		@include grid-column-position( center );
	}
	@include breakpoint( medium ) {
		@include grid-column( 60% );
		@include grid-column-position( center );
	}
	@include breakpoint( large ) {
		@include grid-column( 40% );
		@include grid-column-position( center );
	}
}

.emphasisblock__paragraph {
	padding: $global-padding / 2 $global-padding;
	p:last-child,
	ul:last-child {
		margin-bottom: 0.25 * $global-margin;
	}
}

.emphasisblock__subheader {
	margin-top: $global-margin;
}

.section__emphasisblock li {
	list-style: none;
	background-image: url('data:image/png;base64,');
	background-size: 0.75 * $global-padding auto;
	background-position: top 0.25 * $global-padding left;
	background-repeat: no-repeat;
	padding-left: 1.5 * $global-padding;
	margin-top: 0.75 * $global-margin;
}

.emphasisblock--default {
	background-color: $yellow-1;

}

