/*********************
ARTICLE META STYLES
*********************/

.asides-wrapper {
	width: 100%;
	position: absolute;
}

.story__meta-wrapper {
	@include grid-column-row;
}

.story__meta {
	@include show-for( xlarge );
	z-index: -5;
}

.story__meta-inner {
	width: 100%;
	padding: $global-padding;
	background-color: $yellow-1;
	text-align: right;
}

.story__meta__category,
.story__meta__date {
	font-size: $small-font-size;
}

.story__meta__category {
	font-weight: $global-weight-semibold;
}

.story__meta__tags {
	margin-top: $global-margin;
}

/*********************
ARTICLE EXTRAS STYLES
*********************/

.story__extras {
	background-color: $salmon-1-web;
}

.relatedgrid__griditem {
	@include grid_column( 12 );
	@include breakpoint( medium ) {
		@include grid_column( 6 );
	}
	@include breakpoint( large ) {
		@include grid_column( 4 );
	}
	margin-bottom: $global-margin;
}

.griditem__content {
	padding: 0.5 * $global-padding;
	background-color: $white;
}

.griditem__image {
	margin: 0;
	width: 100%;
	max-height: 10em;
	overflow: hidden;
}

.griditem__title {
	margin-top: 1 * $global-margin;
}

.griditem__intro, .griditem__editorialintro {
}



